import React, { Component } from 'react';
import { Switch, Route } from 'react-router-dom';
import Header from './layout/Header';
import Footer from './layout/Footer';

import Home from './Home';
import About from './About';
import Projects from './Projects';
import Contact from './Contact';
import ProjectsDetail from './ProjectsDetail';
import CategoryList from './layout/CategoryList'
import NotFoundPage from './NotFoundPage';

import data from './data/data.json'

class Main extends Component {
    state = data

    componentDidMount() {
        const { data, categoryList } = this.state;
        for(let i=0; i<data.length; i++) {
            categoryList.push(data[i].categories);
        }
        //const flatCategoryList = categoryList.flat();
        const flatCategoryList = categoryList.reduce((item, val) => item.concat(val), []);
        this.setState({
            // categoryList : [...new Set(categoryList.flat())]
            categoryList : flatCategoryList.filter((item, index) => flatCategoryList.indexOf(item) === index),
            hash : window.location.hash
        });

        /* 줮같은 IE 판별 */
        function isIE () {
            var myNav = navigator.userAgent.toLowerCase();
            return (myNav.indexOf('msie') !== -1) ? parseInt(myNav.split('msie')[1]) : false;
        }
        console.log("ieVersion : " + isIE());
        if(isIE() <= 9 && isIE() !== false) {
            document.querySelector('.Main').classList.add("ie9");
        }
    }

    hashChange = (data) => {
        this.setState({
            hash : data,
            isContactActive : false
        }, () => {
            const $pageWiper = document.querySelector('.page-wiper');
            const $wrapper = document.querySelector('.wrapper');
            window.scrollTo(0,0);
            $wrapper.style.opacity = "0";
            $pageWiper.classList.add("transition");
            $pageWiper.classList.add("active");
            this.closeMobileNav();
            
            setTimeout(function(){
                $pageWiper.classList.add("deactive");
                $wrapper.style.opacity = "1";
            },600);

            setTimeout(function(){
                $pageWiper.classList.remove("transition");
                $pageWiper.classList.remove("active");
                $pageWiper.classList.remove("deactive");
            },1200);
        });
    }

    activeContact = () => {
        this.setState({
            isContactActive : true
        });
    };
    deactiveContact = () => {
        this.setState({
            isContactActive : false
        });
    };

    closeMobileNav = () => {
        const $mobileNav = document.querySelector('.mobile-nav');
        $mobileNav.style.right = "-100%";
        $mobileNav.setAttribute("aria-expanded",false);
        setTimeout(function(){
            $mobileNav.style.display = "none";
        },300);
    }

    render() {
        const { data, categoryList, hash } = this.state;
        return(
            <section className="Main">
                {this.state.isContactActive && <Contact onDeActiveContact={this.deactiveContact}/>}
                <Header hash={hash} onClickContact={this.activeContact}/>
                <div className="page-wiper"></div>
                <div className="wrapper">
                    <Switch>
                        <Route exact path="/" render={props => {return <Home data={data} onHashChange={this.hashChange}/>}}></Route>
                        <Route path="/about" render={props => {return <About onHashChange={this.hashChange}/>}}></Route>
                        <Route path="/projects" render={props => {return <Projects data={data} onHashChange={this.hashChange} categoryList={categoryList}/>}}></Route>

                        {/* Projesct Detail Page */}
                        {data.map((item, index) => {
                            const next = data[index - 1];
                            const prev = data[index + 1];
                            return <Route path={"/detail/"+item.id} render={props => { return <ProjectsDetail data={item} prev={prev} next={next} onHashChange={this.hashChange}/> }} key={item.id}></Route>
                        })}

                        {/* Category List Page */}
                        {categoryList.map((item) => {
                            return <Route path={"/categories/"+item} render={props => {return <CategoryList data={data} category={item} categoryList={categoryList} onHashChange={this.hashChange}/>}} key={item.id}></Route>
                        })}

                        {/* 404 Not Found Page */}
                        <Route path="*" render={props => {return <NotFoundPage onHashChange={this.hashChange}/>}}></Route>
                    </Switch>
                </div>
                <Footer/>
            </section>
        );
    }
}

export default Main;